 .hamburger-menu{
    position: relative;
    display: inline-block;
}
.menu-btn{
    cursor: pointer;
    font-size: 29px;
    margin: 10px;
}
.menu-content{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 35px;
    background-color: white;
    border: 1px solid #ddd;
    width: 200px;
}
.menu-content a{
    text-decoration: none;
    padding: 10px;
    color: black;
    text-align: left;
}
.menu-content a:hover{
    background-color: #ddd;
}
.sub-menu-content{
    margin-left: 20px;
} 

