 .tile-view{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
}
.tile{
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    padding: 20px;
    width: 200px;
    cursor: pointer;
    text-align: left;
}
.tile button{
    margin: 5px 0;
    padding: 5px;
    font-size: 14px;
} 
